<template>
  <div class="about-container" v-loading="isLoading || !srcLoaded">
    <!-- 链接外部网页 -->
    <iframe
      class="about-content"
      :src="src"
      frameborder="0"
      @load="srcLoaded = true"
    ></iframe>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      srcLoaded: false,
    };
  },
  computed: {
    ...mapState("about", {
      src: "data",
      isLoading: "isLoading",
    }),
  },
  created() {
    this.$store.dispatch("about/fetchBanner");
  },
};
</script>

<style scoped lang="less">
.about-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  .about-content {
    width: 100%;
    height: 100%;
    display: block; //这个标签默认是行盒
    box-sizing: border-box;
  }
}
</style>
